html, body, html *, *, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

body.no-scroll {
  overflow: hidden;
}

.no-wrap {
  white-space: nowrap;
}

.wrapper {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
}
.wrapper main {
  flex: 1;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  cursor: pointer;
  background: transparent;
  border: none;
  color: inherit;
}
button:disabled {
  cursor: unset;
}

span, b {
  color: inherit;
  font: inherit;
}

li {
  list-style: none;
}

input, textarea {
  outline: none;
  border: none;
}

input[type=number]::-webkit-outer-spin-button,
input[type=number]::-webkit-inner-spin-button {
  appearance: none;
}

[class*=icon-]::before {
  width: auto;
  margin-left: 0;
  margin-right: 0;
}

* {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-style: normal;
}

::-webkit-scrollbar {
  width: 5px;
  height: 5px;
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: #E0AC03;
  border-radius: 20px;
}

[class*=__container], .container {
  max-width: 1232px;
  padding: 0 16px;
  margin: 0 auto;
}

.certif-icon {
  font-size: 24px;
  color: #0077FF;
}

.map {
  width: 344px;
  height: 258px;
  background: url(../img/basemap.webp) top left;
}
@media (max-width: 740px) {
  .map {
    width: 300px;
  }
}

.ava-upload {
  display: flex;
  flex-direction: column;
  gap: 24px;
  width: 200px;
}
.ava-upload input {
  display: none;
}
.ava-upload__image {
  background: #969696;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 200px;
  height: 175px;
}
.ava-upload__image i {
  font-size: 48px;
}
.ava-upload__image img {
  display: none;
}
.ava-upload.--uploaded .ava-upload__image i {
  display: none;
}
.ava-upload.--uploaded .ava-upload__image img {
  display: flex;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  border-radius: 5px;
}
.ava-upload__btns {
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.ava {
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #969696;
  border-radius: 5px;
  font-size: 48px;
  color: white;
}
.ava img {
  object-fit: cover;
  object-position: center;
  border-radius: 5px;
}
.ava i {
  font-size: 0.5em;
  color: inherit;
  translate: 1px 1px;
}

.btn {
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: #282A2C;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  gap: 8px;
  background: #FCC40F;
  border-radius: 5px;
  transition: 0.3s ease;
}
.btn i {
  font-size: 20px;
  color: inherit;
}
@media (any-hover: hover) {
  .btn:hover {
    background: #E0AC03;
  }
}
.btn.--shadow-white {
  background: white;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.3), 0px 2px 6px 2px rgba(0, 0, 0, 0.15);
}
@media (any-hover: hover) {
  .btn.--shadow-white:hover {
    background: #F6F6F6;
  }
}
.btn.--black {
  background: #282A2C;
  color: white;
}
@media (any-hover: hover) {
  .btn.--black:hover {
    background: #FCC40F;
    color: #282A2C;
  }
}
.btn.--back-page {
  padding: 10px 24px;
  background: #F5F5F5;
  border-radius: 10px;
  color: #000000;
}
@media (any-hover: hover) {
  .btn.--back-page:hover {
    background: #E7E7E7;
  }
}
.btn.--tag {
  padding: 10px;
  background: #F5F5F5;
}
@media (any-hover: hover) {
  .btn.--tag:hover {
    background: #E7E7E7;
  }
}
.btn.--green {
  background: #319F43;
  color: white;
}
@media (any-hover: hover) {
  .btn.--green:hover {
    background: #51D567;
  }
}
.btn.--green-yellow {
  background: #739f31;
  color: white;
}
@media (any-hover: hover) {
  .btn.--green-yellow:hover {
    background: rgb(45, 124, 58);
  }
}
.btn.--red {
  background: #ef4738;
  color: white;
}
@media (any-hover: hover) {
  .btn.--red:hover {
    background: #8e2a21;
  }
}
.btn.--white {
  background: #FFFFFF;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.3), 0px 2px 6px 2px rgba(0, 0, 0, 0.15);
}
@media (any-hover: hover) {
  .btn.--white:hover {
    background: #E7E7E7;
  }
}
.btn.--add-file input {
  display: none;
}
.btn.--white-solid {
  background: #FFFFFF;
}
@media (any-hover: hover) {
  .btn.--white-solid:hover {
    background: #E7E7E7;
  }
}

.date-select {
  position: relative;
  --drop-left: auto;
  --drop-right: 0;
}
.date-select__dropdown {
  position: absolute;
  z-index: 999;
  top: calc(100% + 5px);
  left: var(--drop-left);
  right: var(--drop-right);
  display: flex;
  flex-direction: column;
  background: #FFFFFF;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.3), 0px 2px 6px 2px rgba(0, 0, 0, 0.15);
  border-radius: 5px;
  width: 318px;
  transition: 0.4s ease;
  transform-origin: top;
}
.date-select:not(.--opened) .date-select__dropdown {
  pointer-events: none;
  transform: scaleY(0);
  opacity: 0;
}
.date-select .form-input, .date-select .form-input input {
  cursor: pointer;
}
.date-select.--opened .form-input button i {
  transform: scaleY(-1);
}
.date-select__dropdown-tabs {
  padding: 6px 10px;
  display: flex;
  gap: 8px;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid #D0D0D0;
}
.date-select__list {
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  max-height: 234px;
  padding-bottom: 70px;
}
.date-select__list:not(.--choosed) {
  display: none;
}
.date-select__item {
  display: flex;
  padding: 6px 10px;
  background: #FFFFFF;
  border-bottom: 1px solid #D0D0D0;
  cursor: pointer;
  transition: 0.3s ease;
}
@media (any-hover: hover) {
  .date-select__item:hover {
    background: #F6F6F6;
  }
}
.date-select__accept-btn {
  position: absolute;
  bottom: 20px;
  align-self: center;
  width: 200px;
}

.diagram-pie {
  position: relative;
  aspect-ratio: 1/1;
  width: 205px;
}
.diagram-pie__segments {
  position: absolute;
  inset: 32px;
  border: 2px solid white;
  border-radius: 50%;
}
.diagram-pie__segments div {
  position: absolute;
  display: block;
  inset: 0%;
  background: conic-gradient(red 60% 0, transparent 0 0);
  border-radius: 50%;
}
.diagram-pie__segment-strokes {
  position: absolute;
  inset: 32px;
}
.diagram-pie__segment-strokes div {
  position: absolute;
  top: 0;
  bottom: 50%;
  width: 0;
  left: 50%;
  border-right: 2px solid white;
  transform-origin: bottom;
  transform: rotate(0deg);
}
.diagram-pie__legend-strokes {
  position: absolute;
  inset: 32px;
}
.diagram-pie__legend-strokes div {
  position: absolute;
  top: -17px;
  bottom: 50%;
  width: 0;
  left: 50%;
  border-right: 1px solid #282A2C;
  transform-origin: bottom;
  transform: rotate(0deg);
}
.diagram-pie__segment-labels {
  position: absolute;
  inset: 32px;
}
.diagram-pie__segment-labels div {
  position: absolute;
  top: -17px;
  bottom: 50%;
  left: 50%;
  width: 0;
  transform-origin: bottom;
  transform: rotate(96deg);
}
.diagram-pie__segment-labels div span {
  font-weight: 400;
  font-size: 12px;
  line-height: 12px;
  color: #282A2C;
  padding-inline: 2px;
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  transform: translate(0, -100%) rotate(-96deg);
}

.form-editing {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 24px 16px;
  gap: 10px;
  background: #F6F6F6;
  border-radius: 12px;
  cursor: pointer;
  transition: 0.3s ease;
}
.form-editing.--edit {
  cursor: auto;
}
.form-editing p {
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #000000;
}
.form-editing textarea {
  cursor: inherit;
  resize: none;
  font-weight: 300;
  font-size: 14px;
  line-height: 16px;
  color: #969696;
  background: transparent;
  width: 100%;
  min-height: 32px;
  resize: none;
}
.form-editing.--edit textarea {
  padding: 16px;
  border-radius: 10px;
  background: white;
  border: 1px solid #969696;
}
.form-editing i {
  font-size: 24px;
  color: #969696;
}
.form-editing.--edit i {
  display: none;
}
.form-editing:not(.--edit) button {
  display: none;
}
@media (any-hover: hover) {
  .form-editing:hover {
    background: #E7E7E7;
  }
}

.form-input {
  display: flex;
  align-items: center;
  background: #F6F6F6;
  border-radius: 5px;
  transition: 0.3s ease;
}
.form-input button {
  min-width: 48px;
  height: 19px;
  background: #FFFFFF;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.3), 0px 2px 6px 2px rgba(0, 0, 0, 0.15);
  border-radius: 2px;
  margin-left: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  line-height: 10px;
  color: black;
}
.form-input button input {
  display: none;
}
.form-input button i {
  transition: 0.3s ease;
}
.form-input button span {
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #000000;
  margin-right: 0.3em;
}
.form-input input {
  flex: 1 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #282A2C;
  min-width: 66px;
  background: transparent;
  padding: 10px;
  width: 0;
}
.form-input input::placeholder {
  color: #747474;
  opacity: 1;
  text-align: center;
}
.form-input:has(button) input::placeholder {
  text-align: left;
}
@media (any-hover: hover) {
  .form-input:hover {
    background: #E7E7E7;
  }
}

